<template>
  <div>
    <h4>Books</h4>

    <div v-if="token">
      <b-form-input
        v-model="customer"
        placeholder="Customer ID"
        @keydown.enter="onSubmit"
      ></b-form-input>
    </div>
  </div>
</template>

<script>

  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'Books-books',

    props: {
      book: Object
    },

    /*data() {
      return {
        book: ''
      }
    },*/

    computed: {
      ...mapState([
        'token'
      ])
    },

    methods: {
      ...mapActions([
        'postRental'
      ]),

      onSubmit() {
        this.$socket.emit('customer', { body: this.customer, token: this.token });
        this.customer = '';
      }
    }
  }

</script>



