<template>
  <div id="app">
    <Header :subtitle="subtitle"/>
  </div>
</template>

<script>
  import Header from '@/components/Header.vue';
  import { mapActions } from 'vuex';

  export default {
    name: 'Location-Location',
    
    components: {
      Header
    },

    data() {
        return {
            subtitle: '',
            locID: null
        }
    },

    watch: {
      $route() {
        this.subtitle = this.$route.params.name;
        this.locID = this.$route.params.id;

        this.fetchBooksByLocation(this.locID);
      }
    },

    mounted() {
        this.subtitle = this.$route.params.name;
        this.locID = this.$route.params.id;

        this.fetchBooksByLocation(this.locID);
    },

    methods: {
      ...mapActions([
        'fetchBooksByLocation'
      ])
    }
  }
</script>

