import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    items: [],
    locations: [],
    customers: [],
    books: [],
    rentals: [],
    token: '',
  },
  mutations: {
    addLocations(state, locs) {
    state.locations = locs;
    },

    addCustomers(state, cus) {
      state.customers = cus;
    },

    addBooks(state, bks){
      state.books = bks;
    },

    addRental(state, obj) {
        const book = state.books.filter( item => item.objectID == obj.bookId )[0];
        if (book) {
          const customer = state.customers.filter( item => item.objectID == obj.customerId )[0];
          if(customer){
            state.rentals.push(obj.rental);
          }
          //image.comments.push(obj.comment);

        }
      },

    setToken(state, token) {
      state.token = token;
      localStorage.token = token;
    },

    removeToken(state) {
      state.token = '';
      localStorage.token = '';
    }
  },
  actions: {

    fetchLocations({ commit }) {
    fetch('api/locations')
      .then( obj => obj.json() )
        .then( res => commit('addLocations', res.Locations) );
        alert("Poruka");
  },

  fetchCustomers({ commit }) {
    fetch('api/customers')
      .then( obj => obj.json() )
        .then( res => commit('addCustomers', res.Customers) );
  },

  async fetchBooksByLocation({ commit, state }, locID) {
    const obj = await fetch(`api/books/${locID}`, {headers: { 'Authorization': `Bearer ${state.token}` }});
    const res = await obj.json();

      commit('addBooks',  res.Books);

      commit('setBookIDs', res.objectIDs);
  },

    register({ commit }, obj) {
      fetch('/api_register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      }).then( res => res.json() )
        .then( tkn => commit('setToken', tkn.token) );
    },

    login({ commit }, obj) {
      fetch('/api_login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    }).then( res => res.json() )
      .then( tkn => {
        if (tkn.msg) {
          alert(tkn.msg);
        } else {
          commit('setToken', tkn.token)
        }
      });
    },

    socket_rental({ commit }, msg) {
      const rental = JSON.parse(msg);
      commit('addRental', { bookId: rental.bookId, customerId: rental.customerId });
    }
  }
})
