<template>
  <div>
    <Header :subtitle="subtitle"/>
    <Books :books="books"/>
  </div>
</template>

<script>

  import Header from '@/components/Header.vue';
  import Books from '@/components/Books.vue';
  import { mapActions } from 'vuex';

  export default {
    name: 'Single-Single',

    components: {
      Header,
      Books
    },

    data() {
      return {
        book: null,
        subtitle: ''
      }
    },
    
    methods: {
      ...mapActions([
        'getBook'
      ])
    },

    mounted() {
      this.getBook(this.$route.params.id).then( res => {
        this.book = res;
        this.subtitle = this.book.title;
      });
    }
  }

</script>